.navMenu {
  display: flex;
  justify-content: center;
  height: 50px;
  align-items: center;
  /* max-width: calc(935px + 40px); */
  padding: 5px 0px 10px 0px;
  border-top: solid 1px black;
  border-bottom: solid 1px black;
}

.navTitle {
  flex: 0 360px;
  font-size: 23px;

}

.navInput {
  /* flex: 1; */
  /* width: 100px; */
  width: 340px
}

.navInput>input {
  height: 36px;
  width: 240px;
  /* color: transparent; */
  border: 0px;
  border-radius: 8px;
  background-color: rgb(238, 237, 237);
  flex-direction: column;
  margin-top: 7px;
  background-image: url(./img/searchGlass.svg);
}

.navInput>input::placeholder {
  /* position: 0px; */
  padding-left: 40px;
}

#search:focus {
  background-image: none
}

#search:focus::-webkit-input-placeholder {
  padding-left: 5px;
}

#search {
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: 10px;
}

.navOption {
  margin-top: 7px;
  /* flex: 1; */
  width: 400px;
  display: flex;
}

.subOption>img {
  /* position: 10px; */
  width: 40px;
  height: 40px;
  display: inline-block;
  flex: 0 120px;
  margin-right: 10px;
  cursor: pointer;
}

.section {
  padding-top: 30px;
  display: flex;
  justify-content: center;
  background-color: azure;
  min-height: 1000px;
  height: 100%;
}

.userStore {
  background-color: white;
  border: solid 3px cornsilk;
  width: 530px;
  height: 100px;
  margin-right: 60px;
  margin-bottom: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
}

.userPost {
  border-radius: 4px;
  background-color: yellow;
  width: 530px;
  height: 600px;
}

.newFriend {
  background-color: blue;
  width: 350px;
  height: 400px;
}

.userStoreList {
  margin-left: 15px;
  width: 500px;
  height: 80px;
  display: flex;
}

.userStoreListuserList {
  width: 90px;
  height: 80px;
  /* background-color: darkblue; */
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center
}

.userImage {
  border-radius: 40px;
  background-color: blueviolet;
  width: 60px;
  height: 60px;
  border: solid 1px black;
}

.userStoreListName {
  color: black;
}

.profile {
  width: 1000px;
  height: 1000px;
}

.topProfile {
  width: 950px;
  height: 220px;
  display: flex;
}

.profileImg {
  width: 330px;
  height: 220px;
  display: flex;
  justify-content: center;
}

.profileDeatilsTop {
  display: flex;
  /* justify-content: space-evenly; */
  align-items: center;
  margin: 10px 0 20px 0;
}

.userTagName {
  font-size: 30px;
}

.profileImg>img {
  width: 220px;
  border: 1px solid;
  border-radius: 50%;
}

.profileDetails {
  /* background-color: yellow; */
  display: flex;
  /* justify-content: space-around; */
  flex-direction: column;
  width: 550px;
  height: 220px;
}

.configuration>img {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.userProfileUpdate>button {
  margin-left: 20px;
  border: 1px rgb(213, 207, 207);
  width: 100px;
  height: 30px;
  cursor: pointer;
  /* background-color: ; */
}

.configuration>img {
  margin-left: 20px;
}

.profileDeatilsMiddle {
  display: flex;
}

.profileDeatilsMiddle>div {
  font-size: 18px;
  margin-bottom: 20px;
  margin-right: 40px;
}

.profileDeatilsMiddle>div>span {
  font-weight: bold;
}

.profileDeatilsBottom {
  font-weight: bold;
}

.profileMenu {
  margin-top: 20px;
  border-top: 2px solid green;
  display: flex;
  width: 950px;
  background-color: rgb(205, 238, 238);
  justify-content: center
}

.profileMenu>div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  width: 90px;
  height: 40px;
}

.profileMenuPost {
  border-top: 2px solid pink;
  position: relative;
  bottom: 2px;
  margin-left: -20px;
}

.myPagePosts {
  width: 950px;
  background-color: rgb(167, 211, 187);
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: space-between;
}

.myPagePosts>div {
  width: 293px;
  height: 293px;
  margin-bottom: 40px;
}

.myPagePosts>div>img {}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.modal {
  width: 700px;
  height: 700px;
  background-color: brown;

}

.modal>.modalTitle {
  text-align: center;
  padding: 10px;
  border-bottom: solid 1px;
}

.modal>.modalTitle>span {
  font-size: 20px;
  /* font */
}